// URL du proxy CORS
const PROXY_URL = 'https://flodrama-cors-proxy.onrender.com';
// Chemin de l'API (selon la configuration AWS API Gateway)
const API_PATH = '/proxy'; // Correspond au chemin /{proxy+} dans l'API Gateway
// URL complte de l'API
const API_URL = `${PROXY_URL}${API_PATH}`;

// Variables pour le suivi des tentatives de connexion
let isBackendAvailable = true;
let connectionAttempts = 0;
let lastConnectionCheck = 0;

/**
 * Vrifie si le backend est disponible
 * @returns Promise<boolean>
 */
export async function checkBackendAvailability(): Promise<boolean> {
  // Ne pas vrifier trop frquemment (max une fois toutes les 30 secondes)
  const now = Date.now();
  if (now - lastConnectionCheck < 30000 && connectionAttempts > 0) {
    return isBackendAvailable;
  }
  
  lastConnectionCheck = now;
  
  try {
    // Tenter une requte simple vers le backend
    await axios.get(`${API_URL}/content?category=drama`, { timeout: 5000 });
    isBackendAvailable = true;
    connectionAttempts = 0;
    console.log(' Connexion au backend tablie avec succs');
    return true;
  } catch (error) {
    connectionAttempts++;
    isBackendAvailable = false;
    console.warn(` chec de connexion au backend (tentative ${connectionAttempts})`);
    return false;
  }
}

/**
 * Effectue une requte API avec gestion des erreurs et retry
 * @param url URL de la requte
 * @param retries Nombre de tentatives restantes
 * @returns Promise<any>
 */
async function apiRequest<T>(url: string, retries = 3): Promise<T> {
  try {
    const response = await axios.get<T>(url, { timeout: 10000 });
    return response.data;
  } catch (error: any) {
    if (retries > 0 && error.code !== 'ECONNABORTED') {
      // Attendre avant de ressayer
      await new Promise(resolve => setTimeout(resolve, 1000));
      return apiRequest<T>(url, retries - 1);
    }
    
    // Si toutes les tentatives chouent, marquer le backend comme indisponible
    isBackendAvailable = false;
    throw error;
  }
}

/**
 * Recherche des contenus
 * @param query Terme de recherche
 * @param userId ID de l'utilisateur (optionnel)
 * @returns Promise<ContentItem[]>
 */
export const searchContents = async (query: string, userId?: string): Promise<ContentItem[]> => {
  try {
    // Vrifier si le backend est disponible
    const backendAvailable = await checkBackendAvailability();
    
    if (backendAvailable) {
      try {
        // Construire l'URL avec les paramtres
        let url = `${API_URL}/search?q=${encodeURIComponent(query)}`;
        if (userId) {
          url += `&userId=${encodeURIComponent(userId)}`;
        }
        
        // Tenter de rcuprer les donnes depuis l'API
        const results = await apiRequest<ContentItem[]>(url);
        console.log(` ${results.length} rsultats de recherche rcuprs depuis l'API pour "${query}"`);
        
        // Si aucun rsultat n'est trouv, dclencher un scraping cibl
        if (results.length === 0) {
          console.log(` Aucun rsultat trouv pour "${query}", dclenchement d'un scraping cibl`);
          await triggerTargetedScraping(query, userId);
        }
        
        return results;
      } catch (error) {
        console.warn(` chec de recherche depuis l'API pour "${query}", fallback sur les donnes mockes`, error);
        // Fallback sur les donnes mockes en cas d'erreur
        return searchMockContents(query);
      }
    } else {
      console.warn(` Backend indisponible, utilisation des donnes mockes pour la recherche "${query}"`);
      // Utiliser les donnes mockes si le backend est indisponible
      return searchMockContents(query);
    }
  } catch (error) {
    console.error(`Erreur lors de la recherche pour "${query}":`, error);
    return searchMockContents(query);
  }
}

/**
 * Dclenche un scraping cibl pour une requte spcifique
 * @param query Terme de recherche
 * @param userId ID de l'utilisateur (optionnel)
 * @returns Promise<string> ID de la demande de contenu
 */
export const triggerTargetedScraping = async (query: string, userId?: string): Promise<string> => {
  try {
    // Vrifier si le backend est disponible
    const backendAvailable = await checkBackendAvailability();
    
    if (backendAvailable) {
      try {
        // Construire l'URL avec les paramtres
        let url = `${API_URL}/trigger-scraping?q=${encodeURIComponent(query)}`;
        if (userId) {
          url += `&userId=${encodeURIComponent(userId)}`;
        }
        
        // Dclencher le scraping via l'API
        const response = await axios.post(url);
        const requestId = response.data.requestId;
        
        console.log(` Scraping cibl dclench pour "${query}" (ID: ${requestId})`);
        return requestId;
      } catch (error) {
        console.warn(` chec du dclenchement du scraping cibl pour "${query}"`, error);
        // Gnrer un ID fictif en cas d'erreur
        return `mock-request-${Date.now()}`;
      }
    } else {
      console.warn(` Backend indisponible, impossible de dclencher un scraping cibl pour "${query}"`);
      // Gnrer un ID fictif si le backend est indisponible
      return `mock-request-${Date.now()}`;
    }
  } catch (error) {
    console.error(`Erreur lors du dclenchement du scraping cibl pour "${query}":`, error);
    return `mock-request-${Date.now()}`;
  }
}

/**
 * Recherche dans les donnes mockes
 * @param query Terme de recherche
 * @returns Rsultats de recherche
 */
const searchMockContents = (query: string): ContentItem[] => {
  const normalizedQuery = query.toLowerCase();
  const results: ContentItem[] = [];
  
  // Rechercher dans toutes les catgories
  Object.values(mockData).forEach(categoryItems => {
    const categoryResults = categoryItems.filter(item => {
      // Vrifier le titre
      const titleMatch = item.title.toLowerCase().includes(normalizedQuery);
      
      // Vrifier la synopsis si elle existe (dans ContentDetail)
      const detailItem = item as unknown as ContentDetail;
      const synopsisMatch = detailItem.synopsis ? 
        detailItem.synopsis.toLowerCase().includes(normalizedQuery) : false;
      
      return titleMatch || synopsisMatch;
    });
    results.push(...categoryResults);
  });
  
  return results;
}

